body{
    background: rgb(230, 244, 255);
    padding: 0px;
    margin: 0px;
}
.box{
    margin:10% auto;
    width: 300px;
    height: 300px;
    background: white;
    text-align: center;
    display: flex;
    flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(226, 226, 226);
  box-shadow: black 0px 0px 18px -10px;
  border-radius: 5px;

}
.inputFeild{
    padding: 10px 20px;
    margin: 5px;
    border: 1px solid rgb(112, 112, 112);
    border-radius: 30px;
    outline: 0px;
    font-size: 18px;
    width: 180px;
}
.inputFeild:hover{
    border: 1px solid rgb(255, 255, 255);
    box-shadow: black 0px 0px 18px -10px;
}
.location{
    color: rgb(0, 174, 255);
    font-size: 30px;
    margin: 5px;
}
.temp{
    color: rgb(0, 107, 156);
    font-size: 20px;
    margin: 20px 10px;
}
.tempmin_max{
    color: rgb(0, 40, 58);
    font-size: 20px;
    margin: 5px;
}
